import { NextResponse } from 'next/server';
import connectDB from '@/lib/db';
import User from '@/models/User';
import jwt from 'jsonwebtoken';

export async function GET(req: Request) {
    try {
        await connectDB();

        // 1. Create or find a test user
        let user = await User.findOne({ email: 'test@example.com' });
        if (!user) {
            const hashedPassword = 'password123'; // In real app should be hashed, but for test user creation we might skip if we don't login via UI
            user = await User.create({
                username: 'testuser',
                email: 'test@example.com',
                password: hashedPassword,
                xp: 0,
                level: 1,
                completedLessons: [],
                currentLesson: 1
            });
        }

        // 2. Generate Token
        const payload = {
            user: {
                id: user.id,
            },
        };
        const token = jwt.sign(
            payload,
            process.env.JWT_SECRET || 'fallback_secret',
            { expiresIn: 3600 }
        );

        // 3. Simulate the API call to /api/user/progress
        // We can't easily call the other route function directly because of Request object mocking.
        // So we will use fetch to hit the running server.
        // Assuming running on localhost:3000
        const protocol = req.headers.get('x-forwarded-proto') || 'http';
        const host = req.headers.get('host');
        const apiUrl = `${protocol}://${host}/api/user/progress_v2`;

        console.log('Testing API URL:', apiUrl);

        const res = await fetch(apiUrl, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'x-auth-token': token
            },
            cache: 'no-store',
            body: JSON.stringify({
                lessonId: 1,
                completed: true
            })
        });

        const data = await res.json();
        const status = res.status;

        return NextResponse.json({
            testStatus: status === 200 ? 'SUCCESS' : 'FAILED',
            apiStatus: status,
            apiResponse: data,
            tokenUsed: token
        });

    } catch (err: any) {
        return NextResponse.json({ error: err.message, stack: err.stack }, { status: 500 });
    }
}
